.convertForm {
    display: flex;
    width: 100%;
    margin: 0 0 10px 0;
    position: relative;

    &__btn-select {
        border: 1px solid black;
        
    }

    &__input {
        width: 100%;
        border: 1px solid black;
        padding: 5px;
    }

    &__selectList-hide {
        display: none;
    }

    &__selectList {
        position: absolute;
        top: 30px;
        right: 0;
        background: grey;
        color: white;
        z-index: 3;
    }

    &__selectList li {
        margin: 5px;
    }

}